/* Prismjs end */
/* Tags */
@media (max-width: 575.98px) {
html {
    font-size: 90%;
}
}
body {
  font-weight: 300;
  font-family: 'Lato', sans-serif;
}
a {
  font-weight: 400;
}
p {
  font-size: 1.1rem;
  margin-bottom: 1.5rem;
}
h1 > a,
h2 > a,
h3 > a,
h4 > a,
h5 > a,
h6 > a {
  color: #ffffff;
}
h1 > a:hover,
  h2 > a:hover,
  h3 > a:hover,
  h4 > a:hover,
  h5 > a:hover,
  h6 > a:hover {
    text-decoration: none;
    color: #ffffff;
}
h1 > a:hover::before,
    h2 > a:hover::before,
    h3 > a:hover::before,
    h4 > a:hover::before,
    h5 > a:hover::before,
    h6 > a:hover::before {
      color: #12525e;
}
h1 > a::before,
  h2 > a::before,
  h3 > a::before,
  h4 > a::before,
  h5 > a::before,
  h6 > a::before {
    content: '§';
    color: silver;
    margin-left: -1em;
    position: absolute;
}
@media (max-width: 575.98px) {
h1 > a::before,
      h2 > a::before,
      h3 > a::before,
      h4 > a::before,
      h5 > a::before,
      h6 > a::before {
        display: none;
}
}
code {
  color: #1a7789;
}

/* Tags end */
/* Headings */
.docs-h1 {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  opacity: 0.9;
  margin-left: auto;
  margin-right: auto;
  font-size: 2.7rem;
  padding-right: 1rem;
  text-transform: lowercase;
  background-position: 100%;
  background-size: 1rem auto;
  background-repeat: no-repeat;
  background-image: url("~@/assets/images/logo.png");
  text-shadow: 0 0.25rem 0.5rem rgba(0, 0, 0, 0.3);
}

/* Headings end */
/* Layout */
.docs {
  min-width: 320px;
}
.docs-container {
  position: relative;
}
.docs-container--has-jumbotron {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  background-image: linear-gradient(125deg, #18202a 45%, #12525e 50%, #18202a 80%);
}
.docs-jumbotron {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  color: #ffffff;
}
.docs-version {
  font-weight: 300;
  font-size: 0.7rem;
}
.docs-tagline {
  text-align: center;
}
.docs-stepper {
  width: 100%;
  font-size: 1.1rem;
}
@media (max-width: 575.98px) {
.docs-stepper .v-step .index {
      display: none;
}
}
.docs-button {
  min-width: 6rem;
  font-weight: 300;
  font-size: 1.1rem;
  margin-left: 0.35rem;
  margin-right: 0.35rem;
  color: #ffffff;
  -webkit-transition: -webkit-box-shadow 0.2s;
  transition: -webkit-box-shadow 0.2s;
  transition: box-shadow 0.2s;
  transition: box-shadow 0.2s, -webkit-box-shadow 0.2s;
  background-color: transparent;
  border: 1px solid rgba(255, 255, 255, 0.5);
}
.docs-button:hover {
    color: #ffffff;
    -webkit-box-shadow: 0 0.25rem 0.5rem rgba(0, 0, 0, 0.2);
            box-shadow: 0 0.25rem 0.5rem rgba(0, 0, 0, 0.2);
    background-image: linear-gradient(-135deg, rgba(255, 255, 255, 0.5), transparent 100%);
}
@media (max-width: 575.98px) {
.docs-button {
      margin-top: 0.5rem;
}
}
.docs-button-group {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
}
.docs-fixed-anchor {
  left: 0;
  width: 100%;
  bottom: 0.8rem;
  font-weight: 300;
  font-size: 1.2rem;
  position: absolute;
  text-align: center;
  color: #ffffff;
}
.docs-fixed-anchor:hover {
    color: #ffffff;
}
.docs-markdown {
  margin-bottom: 2rem;
}
.docs-footer {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  min-height: 5rem;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  background-image: linear-gradient(125deg, #18202a 25%, #12525e 50%, #18202a 75%);
}
.docs-credit {
  text-align: center;
  color: #ffffff;
}
.docs-credit a {
    color: rgba(255, 255, 255, 0.9);
}
.docs-github-star {
  top: 1rem;
  left: 1rem;
  position: absolute;
}
.docs-github {
  top: 1.2rem;
  right: 1.2rem;
  position: fixed;
  margin-top: -0.175rem;
  margin-right: -0.5rem;
  -webkit-animation: docs-float 6s ease-in-out infinite;
          animation: docs-float 6s ease-in-out infinite;
}
.docs-github img {
    width: 4rem;
    -webkit-transform: scaleX(-1);
            transform: scaleX(-1);
}

/* Layout end */
/* Utils */
.docs-c-pointer {
  cursor: pointer;
}
.docs-100vh {
  height: 100vh;
}
.docs-min-100vh {
  min-height: 100vh;
}
.docs-clearfix::after, .docs-clearfix::before {
  height: 0;
  display: block;
  content: '\0020';
  overflow: hidden;
}
.docs-clearfix::after {
  clear: both;
}

/* Utils end */
/* Animations */
@-webkit-keyframes docs-float {
0% {
    -webkit-transform: translateY(0);
            transform: translateY(0);
}
50% {
    -webkit-transform: translateY(-0.5rem);
            transform: translateY(-0.5rem);
}
100% {
    -webkit-transform: translateY(0);
            transform: translateY(0);
}
}
@keyframes docs-float {
0% {
    -webkit-transform: translateY(0);
            transform: translateY(0);
}
50% {
    -webkit-transform: translateY(-0.5rem);
            transform: translateY(-0.5rem);
}
100% {
    -webkit-transform: translateY(0);
            transform: translateY(0);
}
}

/* Animations end */



